import React, { useEffect, useMemo, useState } from "react";
import Header from "../../../shared/components/header";
import { StatusConnection } from "./components/StatusConnection";
import NetworkInfo from "./components/NetworkInfo";
import {
  ACCOUNT_TYPE,
  CONNECTION_STATES,
  TYPE_NETWORK,
} from "../../../constants/enum";
import EarnBoxInfo from "./components/EarnBoxInfo";
import { IconCheckSuccess } from "../../../assets/icon";
import AuthService from "../../../adapter/services/auth";
import ReferralLink from "./components/ReferralLink";
import InactiveScreen from "./components/InactiveScreen";
import classNames from "classnames";
import { Bounce, ToastContainer, toast } from "react-toastify";
import Loading from "../../../shared/components/loading";
import { useNavigate } from "react-router-dom";
import "./style.css";
import {
  accountInfoState,
  ipState,
} from "../../../adapter/recoil/common/state";
import { useRecoilState, useRecoilValue } from "recoil";
import browser from "webextension-polyfill";
import { MAP_NETWORD_DESCRIPTION, ROUTE } from "../../../constants";
import AIService from "../../../adapter/services/ai";
import { apiState } from "../../../adapter/recoil/ai/state";
import { ROUTER_EXTENSION } from "../../../constants/router";
import SummaryBox from "./components/SummaryBox";
import ModalPrivacy from "./components/ModalPrivacy";
import LockNetworkModal from "./components/LockNetworkModal";
import { extractText } from "../../../services/extractText";
import CircularProgressChart from "./components/CircularProgressChart";
import ModalNotificationSignal from "./components/ModalNotificationSignal";
import { BgBannerUpgrade } from "../../../assets/image";
declare const chrome: any;

const listColor = {
  [TYPE_NETWORK.HIGH]: ["#39D435", "#39D435", "#26BE97"],
  [TYPE_NETWORK.MEDIUM]: ["#E7C316", "#FDF77D", "#E7C316"],
  [TYPE_NETWORK.LOW]: ["#F5222D", "#95181F", "#F5222D"],
  [TYPE_NETWORK.NONE]: ["#F5222D", "#95181F", "#F5222D"],
};

const NetworkConnectionView = () => {
  const navigate = useNavigate();
  const [accountInfo, setAccountInfo] = useRecoilState(accountInfoState);
  const [networkAvailable, setNetworkAvailable] = useState<boolean>();
  const [ipScore, setIPScore] = useState<number>();
  const [api, setApiState] = useRecoilState(apiState);
  const [isAgree, setIsAgree] = useState(false);
  const [modalProvicy, setModalProvicy] = useState(false);
  const [isActiveFeature, setIsActiveFeature] = React.useState(true);
  const [isActiveNetwork, setIsActiveNetwork] = React.useState(true);
  const init = () => {
    browser.storage.local.get(["np_session"]).then((data) => {
      let np_session_info = data?.["np_session"]?.np_session_info;
      setIsActiveFeature(np_session_info?.features?.ai_summary_access || false);
      setIsActiveNetwork(np_session_info?.features?.network_access || false);
    });
  };
  useEffect(() => {
    init();
  }, []);
  const typeNetwork = useMemo(() => {
    return networkAvailable
      ? ipScore >= 75
        ? TYPE_NETWORK.HIGH
        : ipScore > 25
        ? TYPE_NETWORK.MEDIUM
        : TYPE_NETWORK.LOW
      : TYPE_NETWORK.NONE;
  }, [networkAvailable, ipScore]);
  function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    // The maximum is inclusive and the minimum is inclusive
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  browser.storage?.onChanged.addListener(async (changes) => {
    if (changes["status_ws"] || changes["ip_score_ws"]) {
      getNetworkInfoFromService();
    }
  });

  const getNetworkInfoFromService = async () => {
    let statusWebsocketInfo = await browser.storage.local.get(["status_ws"]);
    let ipScore = await browser.storage.local.get(["ip_score_ws"]);

    if (statusWebsocketInfo?.status_ws) {
      if (statusWebsocketInfo?.status_ws == CONNECTION_STATES.CONNECTED) {
        setNetworkAvailable(true);
        setIPScore(ipScore?.ip_score_ws || getRandomInt(80, 99));
      } else if (
        statusWebsocketInfo?.status_ws === CONNECTION_STATES.DISCONNECTED
      ) {
        setNetworkAvailable(false);
        setIPScore(0);
      } else if (
        statusWebsocketInfo?.status_ws === CONNECTION_STATES.NONE_CONNECTION
      ) {
        browser.storage.local.remove(["np_token", "np_session"]);
        browser.runtime.sendMessage({
          status_account: "LOGOUT",
        });
        navigate(ROUTE.LOGIN);
      }
    } else {
      setNetworkAvailable(false);
      setIPScore(0);
    }
  };

  useEffect(() => {
    getNetworkInfoFromService();
    AIService.getQuota().then((res) => {
      if (res.code === 0) {
        setApiState((prev) => ({
          ...prev,
          remain_summary_quota: res.data?.remain_summary_quota,
        }));
      } else {
      }
    });
  }, []);
  useEffect(() => {
    chrome.storage.local.get("isAgree", (result: { isAgree: boolean }) => {
      if (result.isAgree === true) {
        setIsAgree(true);
      }
    });
  }, []);
  const handleCopyLink = () => {
    toast.info(
      <span className="text-14px text-neutral-110 font-bold">
        Referral Link Copied
      </span>,
      {
        position: "top-center",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "light",
        transition: Bounce,
        icon: <img src={IconCheckSuccess} width={24} height={26} />,
        closeButton: false,
        className: "toast-noti-copy",
      }
    );
    navigator.clipboard.writeText(accountInfo?.referral_link);
  };

  const handleClickActivate = () => {
    AuthService.activeAccount({})
      .then((res) => {
        if (res.code === 0) {
          setAccountInfo({
            ...accountInfo,
            state: ACCOUNT_TYPE.ACTIVE,
          });
        } else {
          toast.error(res.msg, {
            position: "top-center",
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Bounce,
            closeButton: false,
          });
        }
      })
      .catch((error) => {
        toast.error(error.msg, {
          position: "top-center",
          autoClose: 3000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Bounce,
          closeButton: false,
        });
      });
  };

  const handleSummaryCurrentTab = async () => {
    if (!isAgree) {
      setModalProvicy(true);
    } else {
      const tabs = await chrome.tabs.query({
        active: true,
        currentWindow: true,
      });
      const currentTab = tabs[0];
      if (!currentTab || !currentTab?.id) {
        return;
      }
      try {
        await extractText(currentTab.id);
        navigate(ROUTER_EXTENSION.SUMMARY_INFO);
      } catch (error) {
        toast.error(
          error.message || "Failed to extract text from the current tab.",
          {
            position: "top-center",
            autoClose: 10000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Bounce,
            closeButton: false,
            className: "w-[300px]",
          }
        );
      }
    }
  };
  const handleSummaryHistory = async () => {
    navigate(ROUTER_EXTENSION.SUMMARY_HISTORY);
  };
  const handleCheckboxAgree = (e: React.ChangeEvent<HTMLInputElement>) => {
    setIsAgree(e.target.checked);
  };
  const handleConfirmSummarize = () => {
    chrome.storage.local.set({ isAgree: isAgree });
    navigate(ROUTER_EXTENSION.SUMMARY_INFO);
  };
  if (!accountInfo) {
    return (
      <div className="layout flex items-center justify-center">
        <Loading />
      </div>
    );
  }
  return (
    <div className="layout">
      <Header state={accountInfo?.state} avatar={accountInfo?.avatar} />
      <div className="flex flex-col min-h-[535px]">
        {/* <div className="bg-[#F1F3FF] py-3 px-4 h-[176px] relative">
          {networkAvailable ? (
            <div className="flex flex-col gap-4 h-full">
              <div className="flex justify-between gap-4">
                <div className="flex flex-col gap-2">
                  <StatusConnection
                    isConnected={networkAvailable}
                    typeNetwork={typeNetwork}
                  />
                  <div className="text-14px opacity-75 text-neutral-110">
                    {MAP_NETWORD_DESCRIPTION[typeNetwork]}
                  </div>
                </div>
                <CircularProgressChart
                  className=""
                  percentage={ipScore ? Number(ipScore) : 0}
                  color={listColor[typeNetwork]}
                />
              </div>
              <EarnBoxInfo points={accountInfo?.balance?.total_collected} />
              <LockNetworkModal open={!isActiveNetwork} />
            </div>
          ) : (
            <div className="h-full flex items-center justify-center">
              <Loading />
            </div>
          )}
        </div>
        <div
          className={classNames({
            hidden: accountInfo?.state !== ACCOUNT_TYPE.INACTIVE,
          })}
        >
          <InactiveScreen onClick={handleClickActivate}></InactiveScreen>
        </div>
        <div
          className={classNames({
            hidden: !accountInfo,
          })}
        >
          <ReferralLink
            handleSummaryCurrentTab={handleSummaryCurrentTab}
            handleCopyLink={handleCopyLink}
            link={accountInfo?.referral_link}
          />
        </div> */}
        <div className="w-full flex justify-center items-center pt-4">
          <div
            className="bg-black rounded-2xl"
            style={{
              backgroundImage: `url(${BgBannerUpgrade})`,
              backgroundSize: "cover",
              backgroundRepeat: "no-repeat",
              backgroundPosition: "center center",
              width: 331,
              height: 348,
            }}
          ></div>
        </div>
        <SummaryBox
          isActiveFeature={isActiveFeature}
          handleSummaryCurrentTab={handleSummaryCurrentTab}
          handleSummaryHistory={handleSummaryHistory}
        />
      </div>
      <ModalPrivacy
        open={modalProvicy}
        isAgree={isAgree}
        handleCheckboxAgree={handleCheckboxAgree}
        handleConfirmSummarize={handleConfirmSummarize}
        onClose={() => setModalProvicy(false)}
      ></ModalPrivacy>
      {/* <ModalNotificationSignal /> */}
      <ToastContainer className="toast-notify" />
    </div>
  );
};

export default NetworkConnectionView;
