import React, { useEffect, useRef, useState } from "react";
import {
  IconBack2,
  IconSummarize,
  IconSummarizeBlack,
  IconSummarizeDisable,
  IconSummarizeWhite,
} from "../../../assets/icon";
import { Bounce, ToastContainer, toast } from "react-toastify";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import Loading from "../../../shared/components/loading";
import { useNavigate } from "react-router-dom";
import "./style.css";
import { accountInfoState } from "../../../adapter/recoil/common/state";
import { useRecoilState } from "recoil";
import { apiState } from "../../../adapter/recoil/ai/state";
import ButtonCustom from "../../../shared/components/button";
import { extractText } from "../../../services/extractText";
import { IPostGenerateSummary } from "../../../adapter/services/ai";
import { fetchDataAI } from "../../../shared/utils/action-summary";
import LoadingSummary from "../components/LoadingSummary";
import { ROUTER_EXTENSION } from "../../../constants/router";
import "./style.css";
import IconCopy from "../../ActiveScreen/views/components/IconCopy";
declare const chrome: any;

const HeaderSummary = () => {
  const navigate = useNavigate();
  return (
    <div className="bg-white p-4 border-b border-grey-20 flex items-center justify-center relative">
      <img
        src={IconBack2}
        alt="ic-back2"
        className="absolute top-4 left-4 cursor-pointer"
        onClick={() => navigate(ROUTER_EXTENSION.DASHBOARD)}
      />
      <span className="text-14px">Summary</span>
    </div>
  );
};

const SummaryInfoView = () => {
  const navigate = useNavigate();
  const [accountInfo, setAccountInfo] = useRecoilState(accountInfoState);
  const [countdown, setCountdown] = useState(0);
  const [api, setApiState] = useRecoilState(apiState);
  const disableButtonSummarize = api.remain_summary_quota <= 0 || countdown > 0;
  const contentRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    handleSummaryCurrentTab();
  }, []);
  useEffect(() => {
    if (countdown > 0) {
      const timer = setTimeout(() => setCountdown(countdown - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [countdown]);
  const handleError = (message: any) => {
    const msg =
      typeof message === "string"
        ? message
        : "Something went wrong. Please try again later.";
    toast.error(msg, {
      position: "top-center",
      autoClose: 3000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
      transition: Bounce,
      closeButton: false,
      className: "w-[300px]",
    });
    setCountdown(10);
  };
  const handleSummaryCurrentTab = async () => {
    if (api.remain_summary_quota <= 0) {
      navigate(ROUTER_EXTENSION.DASHBOARD);
      toast.error(
        "You have reached your limit of 10 summaries. Your limit will reset tomorrow. Please upgrade or wait until then to continue.",
        {
          position: "top-center",
          autoClose: 5000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Bounce,
          closeButton: false,
          className: "w-[300px]",
        }
      );
      return;
    }
    const tabs = await chrome.tabs.query({ active: true, currentWindow: true });
    const currentTab = tabs[0];
    if (!currentTab || !currentTab?.id) {
      return;
    }
    try {
      const result = await extractText(currentTab.id);
      const data: IPostGenerateSummary = {
        content: result,
        title: currentTab?.title,
        tab_url: currentTab?.url,
      };
      fetchDataAI(data, setApiState, handleError);
    } catch (error) {
      toast.error(
        error.message || "Failed to extract text from the current tab.",
        {
          position: "top-center",
          autoClose: 10000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "light",
          transition: Bounce,
          closeButton: false,
          className: "w-[300px]",
        }
      );
    }
  };
  const handleCopyResult = () => {
    if (api.summary) {
      if (contentRef.current) {
        const text = contentRef.current.innerText;
        navigator.clipboard.writeText(text).then(() => {
          toast.success("Summary copied to clipboard!", {
            position: "top-center",
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Bounce,
            closeButton: false,
          });
        });
      }
    }
  };
  if (!accountInfo) {
    return (
      <div className="layout flex items-center justify-center">
        <Loading />
      </div>
    );
  }

  if (api.loading) {
    return (
      <div className="layout">
        <HeaderSummary />
        <div className="min-h-[535px] p-4">
          <LoadingSummary />
        </div>
      </div>
    );
  }

  if (api.summary) {
    return (
      <div className="layout relative">
        <HeaderSummary />
        <div className="flex flex-col max-h-[440px] overflow-y-auto">
          <div
            className="text-14px break-words text-neutral-110 markdown-value p-4"
            ref={contentRef}
          >
            <ReactMarkdown remarkPlugins={[remarkGfm]}>
              {api.summary.replace(/^<\/?think>\s*/i, "").replace(/\\n/g, "\n")}
            </ReactMarkdown>
          </div>
        </div>

        <div className="absolute bottom-0 right-0 p-4 w-full shadow-custom bg-white">
          {api.remain_summary_quota > 0 && (
            <div className="flex mb-2 gap-1 justify-between">
              {/* <img src={IconSummarizeBlack} alt="ic-summarize" className="" /> */}
              <span>
                Remaining summaries: <strong>{api.remain_summary_quota}</strong>
              </span>
              <div
                className="text-14px font-normal text-neutral-70 flex gap-1 cursor-pointer hover:text-neutral-110 group"
                onClick={handleCopyResult}
              >
                <IconCopy className="w-5 h-5"/>
                Copy
              </div>
            </div>
          )}
          <ButtonCustom
            text={
              countdown > 0
                ? `New Summarize in ${countdown}s`
                : "Summarize Another Tab"
            }
            onClick={handleSummaryCurrentTab}
            prefixIcon={
              disableButtonSummarize ? IconSummarizeDisable : IconSummarizeWhite
            }
            disabled={disableButtonSummarize}
          ></ButtonCustom>
          {api.remain_summary_quota > 0 ? (
            <></>
          ) : (
            <div className="text-14px text-yellow break-words mt-3">
              You've used all your summaries! We hope you found them helpful!
              Thank you for testing.
            </div>
          )}
        </div>
        <ToastContainer className="toast-notify" />
      </div>
    );
  }
};

export default SummaryInfoView;
