import React from "react";
import { useNavigate } from "react-router-dom";
import { IconBack2 } from "../../../assets/icon";
import { ROUTER_EXTENSION } from "../../../constants/router";
import useGetHistory from "../../../shared/hook/useGetHistory";
import HeaderSummary from "../components/HeaderSummary";
import SearchInput from "../components/SearchInput";
import EmptyData from "../components/EmptyData";
import HistoryItem from "../components/HistoryItem";
import dayjs from "dayjs";

const SummaryHistoryView = () => {
  const navigate = useNavigate();
  const [params, setParams] = React.useState({
    from: dayjs().subtract(30, "day").toISOString(),
    to: dayjs().subtract(-1, "day").toISOString(),
    page: 0,
    limit: 20,
    search: "",
  });
  const { listGroupedData, groupedData, data, total, loading } =
    useGetHistory(params);
  const handleSetFilter = (search: string) => {
    setParams((prev) => ({ ...prev, page: 0, limit: 20, search }));
  };
  const handleScroll = (event) => {
    const bottom =
      event.target.scrollHeight - event.target.scrollTop >=
        event.target.clientHeight - 1 &&
      event.target.scrollHeight - event.target.scrollTop <
        event.target.clientHeight + 1;
    const isFetch = total <= data.length;
    if (bottom && !isFetch) {
      setParams((prev) => ({ ...prev, page: prev.page + 1, limit: 20 }));
    }
  };
  return (
    <div className="layout">
      <HeaderSummary />
      <div className="px-4 py-3 flex flex-col gap-[10px] bg-white w-full">
        <SearchInput filter={params.search} setFilter={handleSetFilter} />
        <div className="text-12px text-neutral-70">
          History shows the last 30 days
        </div>
      </div>
      <div>
        {loading ? (
          <div>Loading...</div>
        ) : data?.length > 0 ? (
          <div
            className="flex flex-col bg-white w-full h-[450px] overflow-y-auto"
            onScroll={handleScroll}
          >
            {listGroupedData?.map((item, index) => {
              return (
                <div key={index} className="flex flex-col">
                  <div className="text-neutral-110 text-14px px-4 h-10 bg-purple-lighter font-extrabold flex items-center">
                    {item}
                  </div>
                  <div className="flex flex-col gap-0 divide-y divide-neutral-20">
                    {groupedData[item].map((itemHistory, index) => (
                      <HistoryItem
                        key={index}
                        item={itemHistory}
                        handleClickHistoryItem={() =>
                          navigate(ROUTER_EXTENSION.SUMMARY_HISTORY_DETAIL, {
                            state: { data: itemHistory },
                          })
                        }
                        keyword={params.search}
                      />
                    ))}
                  </div>
                </div>
              );
            })}
          </div>
        ) : (
          <EmptyData filter={params.search} />
        )}
      </div>
    </div>
  );
};

export default SummaryHistoryView;
