import {  SetterOrUpdater } from "recoil";
import AIService, { IPostGenerateSummary } from "../../adapter/services/ai";
export const fetchDataAI = async (
  _data: IPostGenerateSummary,
  setApiState: SetterOrUpdater<any>,
  handleError: (error: any) => void
) => {
  setApiState((prev) => ({ ...prev, loading: true, error: null }));
  try {
    AIService.postGenerateSummary(_data)
      .then((res) => {
        if (res.code === 0) {
          const result = res.data;
          setApiState((prev) => ({
            ...prev,
            loading: false,
            summary: result?.summary,
            remain_summary_quota: result?.remain_quota,
            error: null,
          }));
        } else {
          setApiState((prev) => ({
            ...prev,
            loading: false,
            summary: null,
            error: res.msg,
          }));
          handleError(res.msg);
        }
      })
      .catch((error) => {
        setApiState((prev) => ({
          ...prev,
          loading: false,
          summary: null,
          error: error.message,
        }));
        handleError(error);
      });
    // .finally(() => {});
  } catch (error) {
    setApiState((prev) => ({
      ...prev,
      loading: false,
      data: null,
      error: error.message,
    }));
    handleError(error);
  }
};
