import { useEffect, useState } from "react";
import { IconSearch } from "../../../assets/icon";

export default function SearchInput({ filter, setFilter }) {
  const [inputValue, setInputValue] = useState(filter);

  useEffect(() => {
    const handler = setTimeout(() => {
      setFilter(inputValue);
    }, 500); 
    return () => {
      clearTimeout(handler);
    };
  }, [inputValue]);

  const handleChange = (e) => {
    setInputValue(e.target.value);
  };

  return (
    <div className="flex flex-col items-start gap-1 w-[328px] h-[40px]">
      <div className="flex flex-row items-center px-3 py-2 gap-2 w-full h-[40px] bg-[#F5F5F8] rounded-lg">
        <img src={IconSearch} alt="icon-search" className="w-6 h-6" />
        <input
          type="text"
          value={inputValue}
          onChange={handleChange}
          placeholder="Type a page name or website URL"
          className="w-full h-[20px] bg-transparent text-[#A2A3A9] text-sm font-normal placeholder-[#A2A3A9] focus:outline-none"
        />
      </div>
    </div>
  );
}
