import { useNavigate } from "react-router-dom";
import { IconBack2 } from "../../../assets/icon";
import { ROUTER_EXTENSION } from "../../../constants/router";

const HeaderSummary = ({title = 'History', callbackRoute = ROUTER_EXTENSION.DASHBOARD}) => {
  const navigate = useNavigate();
  return (
    <div className="bg-white p-4 border-b border-grey-20 flex items-center justify-center relative">
      <img
        src={IconBack2}
        alt="ic-back2"
        className="absolute top-4 left-4 cursor-pointer"
        onClick={() => navigate(callbackRoute)}
      />
      <span className="text-14px">{title}</span>
    </div>
  );
};

export default HeaderSummary;