import React, { MouseEventHandler, useEffect } from "react";
import { IconHistory, IconWebsiteFavicon } from "../../../assets/icon";
import { IHistorySummarize } from "../../../shared/hook/useGetHistory";
import classNames from "classnames";
import { highlightText } from "../../../shared/utils";

const HistoryItem = ({
  item,
  keyword,
  handleClickHistoryItem,
}: {
  item: IHistorySummarize;
  handleClickHistoryItem?: MouseEventHandler<HTMLDivElement>;
  keyword?: string;
}) => {
  const date = new Date(item?.created_at);
  const options: Intl.DateTimeFormatOptions = {
    hour: "2-digit",
    minute: "2-digit",
    hour12: true,
  };
  const time = date.toLocaleTimeString([], options);
  const [imgFavicon, setImgFavicon] = React.useState("");
  useEffect(() => {
    const fetchFavicon = async () => {
        const originalUrl = new URL(item?.tab_url);
        const defaultFavicon = `${originalUrl.origin}/favicon.ico`;
        setImgFavicon(defaultFavicon);
    };
    fetchFavicon();
  }, [item]);
  return (
    <div
      className={classNames(
        "flex flex-col gap-2 py-3 px-4 h-[110px] justify-center group",
        { "cursor-pointer": handleClickHistoryItem }
      )}
      onClick={handleClickHistoryItem}
      title="Click to view this summary"
    >
      <div className="text-neutral-70 text-12px flex gap-1 items-center">
        <img src={IconHistory} alt="icon-history" className="w-4 h-4" />
        {time}
      </div>
      <div className="flex justify-between items-center gap-2">
        <div className="flex flex-col gap-1 items-start w-4/5">
          <h3
            className={classNames(
              "text-14px truncate w-full text-neutral-110 break-words group-hover:text-purple-dark group-hover:font-bold transition-all duration-200",
              { "font-extrabold text-16px": !handleClickHistoryItem }
            )}
            title={item?.title}
          >
            {highlightText(item?.title, keyword) ?? new URL(item?.tab_url)?.hostname}
          </h3>
          <div
            className="text-12px line-clamp-2 w-full text-neutral-70 break-words"
            title={item?.tab_url}
          >
            {highlightText(item?.tab_url, keyword) ?? "https://"}
          </div>
        </div>
        {imgFavicon && (
          <img
            src={imgFavicon}
            alt="favicon"
            className="w-10 h-10 rounded-full ml-2 bg-neutral-20 flex-shrink-0 border border-neutral-20 border-solid"
            onError={(e) => {
              e.currentTarget.onerror = null;
              e.currentTarget.src = IconWebsiteFavicon;
            }}
          />
        )}
      </div>
    </div>
  );
};

export default HistoryItem;
