import API from "../../constants/api";
import { postWithPath } from "../http";

export interface IEventDataTracking {
  event: string;
  params: Record<string, any>;
  debug_mode?: boolean;
  client_id?: string;
}
const TrackingService = {
  postEvent: (data: IEventDataTracking) => {
    return postWithPath(API.TRACKING.POST.EVENT, {}, data);
  },
};
export default TrackingService;
