import React from "react";
import { IconEmpty, IconSearch } from "../../../assets/icon";
const EmptyData = ({ filter }) => {
  if (filter && filter !== "") {
    return (
      <div className="py-8 px-4 flex flex-col items-center justify-center gap-5 w-full bg-white border-t border-neutral-20 rounded-lg">
        <div className="w-14 h-14 bg-[#F5F5F8] rounded-full flex items-center justify-center">
          <img src={IconSearch} alt="icon-empty" className="w-5 h-4" />
        </div>
        <div className="text-14px text-neutral-110 text-center">
          No matching search results found.
        </div>
      </div>
    );
  }
  return (
    <div className="py-8 px-4 flex flex-col items-center justify-center gap-5 w-full bg-white border-t border-neutral-20 rounded-lg">
      <img src={IconEmpty} alt="icon-empty" className="w-[200px] h-[130px]" />
      <div className="text-14px text-neutral-110 text-center">
        You dont have any summaries yet.
        <br />
        Start by visiting a page to generate one!
      </div>
    </div>
  );
};

export default EmptyData;
