declare const chrome: any;
const LIMIT_INPUT = 20000;

export const extractText = async (tabId: number): Promise<string> => {
  if (!chrome?.scripting) {
    throw new Error(
      "chrome.scripting is not available. Ensure this is running in an extension context."
    );
  }

  try {
    const [result] = await chrome.scripting.executeScript({
      target: { tabId },
      func: () => {
        try {
          const EXCLUDED_DOMAINS = [
            //Forums & Comments
            "reddit.com",
            "quora.com",
            "stackexchange.com",
            "stackoverflow.com",
            "disqus.com",

            //Social Media
            "twitter.com", // X
            "facebook.com",
            "instagram.com",
            "tiktok.com",
            "linkedin.com",
            "snapchat.com",
            "pinterest.com",
            "threads.net",

            //Entertainment (Video & Streaming)
            "youtube.com",
            "twitch.tv",
            "netflix.com",
            "hulu.com",
            "disneyplus.com",
            "primevideo.com",
            "spotify.com",
            "soundcloud.com",

            //E-commerce
            "amazon.com",
            "ebay.com",
            "shopify.com",
            "aliexpress.com",
            "walmart.com",
            "bestbuy.com",
            "target.com",
            "etsy.com",
            "mercari.com",

            //Search Engines
            "google.com",
            "bing.com",
            "yahoo.com",
            "duckduckgo.com",
            "baidu.com",

            //Logins/Registration Pages
            "login.",
            "accounts.",
            "signin.",
            "signup.",
            "register.",
            "auth.",
          ];
          const isExcludedPage = (hostname: string) => {
            try {
              return EXCLUDED_DOMAINS.some((domain) => {
                return hostname?.includes(domain);
              });
            } catch (error) {
              return false;
            }
          };
          if (isExcludedPage(window.location.hostname)) {
            return {
              error:
                "We're unable to summarize this page. It may be too short or contain too much non-text content. Please try a different page with more text.",
            };
          }

          const tempBody = document.body.cloneNode(true) as HTMLElement;

          // clear not-used elements
          tempBody
            .querySelectorAll(
              "script, style, noscript, meta, link, iframe, svg, img, video, audio"
            )
            .forEach((el) => el.remove());
          tempBody
            .querySelectorAll("video, audio, img, iframe, ins, object, svg")
            .forEach((el) => el.remove());
          let text = tempBody.innerText.replace(/\s+/g, " ").trim() || "";

          // cleanup
          text = text
            .replace(/\b(class|id)\s*=\s*["'][^"']*["']/gi, "")
            .replace(/\s?\d{4}.*?|Copyright.*?$/gim, "")
            .replace(
              /\b(Advertisement|Sponsored|Follow us|Subscribe now)\b/gi,
              ""
            )
            .replace(/https?:\/\/\S+/g, "")
            .replace(
              /\b(FAQ|Contact Us|Disclaimer|Terms of Service|Privacy Policy)\b/gi,
              ""
            )
            .replace(/[^\x20-\x7E\u00A0-\uFFFF]/g, "")
            .trim();
          if (!text) return { error: "Empty Content" };
          return text;
        } catch (err) {
          return { error: "Content access restricted" };
        }
      },
    });

    if (!result || !result.result) {
      throw new Error("Unexpected error while extracting text.");
    }
    if (result.result.error) {
      if (result.result.error === "Empty Content") {
        throw new Error(
          "There is no content available to summarize on this page. Please try another page."
        );
      }
      throw new Error(
        result.result.error ??
          "We're unable to summarize this page. It may be too short or contain too much non-text content. Please try a different page with more text!"
      );
    }
    return result.result.slice(0, LIMIT_INPUT);
  } catch (error) {
    if (error?.message?.includes("Cannot access")) {
      throw new Error(
        "We're unable to summarize this page. It may be too short or contain too much non-text content. Please try a different page with more text!"
      );
    }
    throw new Error(error.message || "Failed to extract text.");
  }
};
