import React, { useRef } from "react";
import HeaderSummary from "../components/HeaderSummary";
import { ROUTER_EXTENSION } from "../../../constants/router";
import { useLocation } from "react-router-dom";
import HistoryItem from "../components/HistoryItem";
import remarkGfm from "remark-gfm";
import ReactMarkdown from "react-markdown";
import { Bounce, toast, ToastContainer } from "react-toastify";
import IconCopy from "../../ActiveScreen/views/components/IconCopy";

const SummaryHistoryDetailView = () => {
  const location = useLocation();
  const contentRef = useRef<HTMLDivElement>(null);
  const { data } = location.state || {};
  const handleCopyResult = () => {
    if (data.summary) {
      if (contentRef.current) {
        const text = contentRef.current.innerText;
        navigator.clipboard.writeText(text).then(() => {
          toast.success("Summary copied to clipboard!", {
            position: "top-center",
            autoClose: 3000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Bounce,
            closeButton: false,
          });
        });
      }
    }
  };
  return (
    <div className="layout">
      <HeaderSummary
        title="Detail"
        callbackRoute={ROUTER_EXTENSION.SUMMARY_HISTORY}
      />
      <HistoryItem item={data} />
      <div
        className="text-12px font-normal text-neutral-70 flex gap-1 cursor-pointer hover:text-neutral-110 absolute right-4 top-[4.5rem] group"
        onClick={handleCopyResult}
      >
        <IconCopy className="w-4 h-4"/>
        Copy
      </div>
      <div className="flex flex-col max-h-[435px] h-[435px] overflow-y-auto bg-purple-lighter">
        <div
          className="text-14px break-words text-neutral-110 markdown-value p-4"
          ref={contentRef}
        >
          <ReactMarkdown remarkPlugins={[remarkGfm]}>
            {data?.summary.replace(/^<\/?think>\s*/i, "").replace(/\\n/g, "\n")}
          </ReactMarkdown>
        </div>
      </div>
      <ToastContainer className="toast-notify" />
    </div>
  );
};

export default SummaryHistoryDetailView;
