import { atom, selector } from "recoil";
import AuthService from "../../services/auth";

export const tokenState = atom({
  key: "tokenState",
  default: undefined,
});

export const accountInfoState = atom({
  key: "accountInfoState",
  default: undefined,
});

export const modalState = atom({
  key: "modalState",
  default: {
    signalNotification: false,
  },
});

export const ipState = atom({
  key: "ipState",
  default: selector({
    key: "rewardReferralInfoSeletor",
    get: async () => {
      let res = await AuthService.getIp();
      return res?.ipString;
    },
  }),
});

export const networkInfoState = atom({
  key: "networkInfoState",
  default: selector({
    key: "networkInfoSelector",
    get: async ({ get }) => {
      let ipInfo = get(ipState);
      let res = await AuthService.getDeviceNetwork({ ip: ipInfo });
      return res?.data;
    },
  }),
});
