import API from "../../constants/api";
import { getWithPath, postWithPath, toJson } from "../http";
export interface IPostGenerateSummary {
  title: string;
  content: string;
  tab_url: string;
}
const AIService = {
  postGenerateSummary: (data: IPostGenerateSummary) => {
    return postWithPath(API.AI.POST.GENERATE_SUMMARY, {}, data);
  },
  getQuota: () => {
    return getWithPath(API.AI.GET.QUOTA);
  },
  getHistory: (params) => {
    return getWithPath(API.AI.GET.HISTORY, params);
  },
};
export default AIService;
