/*global chrome*/
import React, { Suspense, useEffect } from "react";
import "./App.css";
import LoginView from "./modules/Login/views";
import { Route, Routes, useNavigate } from "react-router-dom";
import { ROUTER_EXTENSION } from "./constants/router";
import NetworkConnectionView from "./modules/ActiveScreen/views";
import { useRecoilValue, useSetRecoilState } from "recoil";
import {
  accountInfoState,
  modalState,
  tokenState,
} from "./adapter/recoil/common/state";
import browser from "webextension-polyfill";
import Loading from "./shared/components/loading";
import AuthService from "./adapter/services/auth";
import SummaryInfoView from "./modules/SummaryInfo/views";
import SummaryHistoryView from "./modules/SummaryHistory/views";
import SummaryHistoryDetailView from "./modules/SummaryHistory/views/SummaryHistoryDetail";
import TrackingService from "./adapter/services/tracking";
const INTERVAL_CALL_SESSION = 5 * 60 * 1000; // (miliseconds => 1day)

function App() {
  const navigate = useNavigate();
  const setAccountInfo = useSetRecoilState(accountInfoState);
  const setToken = useSetRecoilState(tokenState);
  const token = useRecoilValue(tokenState);
  const setModalState = useSetRecoilState(modalState);
  let callSessionInterval;
  const navigateToLogin = () => {
    navigate(ROUTER_EXTENSION.LOGIN);
  };

  browser.storage?.onChanged.addListener(async (changes) => {
    if (changes["np_session"]) {
      let dataInfo = changes["np_session"].newValue?.np_session_info;
      if (dataInfo) {
        setAccountInfo(dataInfo);
        dataInfo?.tokenInfo && setToken(dataInfo?.tokenInfo);
        navigate(ROUTER_EXTENSION.DASHBOARD);
      } else {
        navigateToLogin();
      }
    }
  });

  const callAPISession = () => {
    AuthService.postSession({})
      .then((res) => {
        if (res.code === 0 && res.data) {
          browser.storage.local.set({
            np_session: {
              np_session_info: { ...res.data, tokenInfo: token },
              np_session_expire_date: new Date().getTime(),
            },
          });
        } else {
          navigateToLogin();
        }
      })
      .catch((e) => {
        navigateToLogin();
      });
  };

  const triggerAuthen = async () => {
    browser.storage.local
      .get(["np_token"])
      .then((data) => {
        let tokenStorage = data?.["np_token"];
        if (tokenStorage) {
          setToken(tokenStorage);
          callAPISession();
        } else {
          navigateToLogin();
        }
      })
      .catch((e) => {
        navigateToLogin();
      });
  };

  const init = async () => {
    browser.storage.local
      .get(["np_session"])
      .then((data) => {
        let dataInfo = data?.["np_session"];
        let dataInfoAccount = dataInfo?.np_session_info;
        if (dataInfo) {
          setToken(dataInfoAccount?.tokenInfo);
          if (
            new Date().getTime() - dataInfoAccount?.np_session_expire_date * 1 >
            60 * 1000
          ) {
            callAPISession();
          } else {
            setAccountInfo(dataInfoAccount);
            navigate(ROUTER_EXTENSION.DASHBOARD);
          }
        } else {
          triggerAuthen();
        }
      })
      .catch((error) => triggerAuthen());
    const { hasNotificationSignal } = await chrome.storage.local.get(
      "hasNotificationSignal"
    );
    if (hasNotificationSignal) {
      setModalState((prev) => ({
        ...prev,
        signalNotification: true,
      }));
      browser.storage.local.get(["np_session"]).then((data) => {
        let np_session_info = data?.["np_session"]?.np_session_info;
        const user_id = np_session_info?.uid;
        TrackingService.postEvent({
          event: "signals_extension_open",
          params: {
            user_id,
            user_agent: navigator.userAgent,
            timestamp: new Date().toISOString(),
            // debug_mode: true,
          },
          client_id: `${Date.now()}`,
        });
      });
    }
  };

  useEffect(() => {
    chrome.action.setBadgeText({ text: "" });
    chrome.action.setIcon({
      path: {
        16: "/16.png",
        48: "/48.png",
        128: "/128.png",
      },
    });
    init();
    callSessionInterval = setInterval(() => {
      callAPISession();
    }, INTERVAL_CALL_SESSION);
  }, []);

  useEffect(() => {
    return () => {
      clearInterval(callSessionInterval);
    };
  }, []);

  return (
    <Suspense
      fallback={
        <div className="layout flex items-center justify-center">
          <Loading />
        </div>
      }
    >
      <Routes>
        <Route path={ROUTER_EXTENSION.LOGIN} element={<LoginView />} />
        <Route
          path={ROUTER_EXTENSION.DASHBOARD}
          element={<NetworkConnectionView />}
        />
        <Route
          path={ROUTER_EXTENSION.SUMMARY_INFO}
          element={<SummaryInfoView />}
        />
        <Route
          path={ROUTER_EXTENSION.SUMMARY_HISTORY}
          element={<SummaryHistoryView />}
        />
        <Route
          path={ROUTER_EXTENSION.SUMMARY_HISTORY_DETAIL}
          element={<SummaryHistoryDetailView />}
        />
      </Routes>
    </Suspense>
  );
}

export default App;
