import React, { useRef, useState } from "react";
import { ImgAvatarMockup, ImgLogo } from "../../../assets/image/index";
import { IconBack, IconCheck, IconLogout } from "../../../assets/icon";
import { useClickOutside } from "../../hook/useClickOutside";
import classNames from "classnames";
import { ACCOUNT_TYPE } from "../../../constants/enum";
import browser from "webextension-polyfill";
import { useNavigate } from "react-router-dom";
import { ROUTE } from "../../../constants";

const Header = ({ type = "logo", state, avatar }) => {
  const navigate = useNavigate();
  const dropDownMenu = useRef<any>(null);
  const [showDropdown, setShowDropdown] = useState<boolean>(false);

  useClickOutside(dropDownMenu, () => {
    setShowDropdown(false);
  });

  const renderHeaderContent = () => {
    switch (type) {
      case "logo":
        return <img src={ImgLogo} alt="logo" />;
      case "back":
        return (
          <div className="flex items-center">
            <img src={IconBack} alt="ic-back" />
            &nbsp;<span className="text-black">Back</span>
          </div>
        );
    }
  };

  const renderAccountType = () => {
    switch (state) {
      case ACCOUNT_TYPE.ACTIVE:
        return (
          <div className="rounded-lg px-3 py-1 bg-green text-white text-12px">
            Activated
          </div>
        );
      case ACCOUNT_TYPE.INACTIVE:
        return (
          <div className="rounded-lg px-3 py-1 bg-grey-20 text-neutral-110 text-12px">
            Not Activated
          </div>
        );
    }
  };
  const handleLogout = () => {
    browser.storage.local.remove(["np_token", "np_session"]);
    browser.runtime.sendMessage({
      status_account: "LOGOUT",
    });
    navigate(ROUTE.LOGIN);
  };
  return (
    <div className="bg-white px-5 py-4 border-b border-grey-20 flex items-center justify-between h-12">
      {renderHeaderContent()}
      <div className="relative w-fit">
        <div className="flex items-center justify-end">
          <div className="mr-2"> {renderAccountType()}</div>
          <div className="w-8 h-8 rounded-full">
            <img width={32} height={32} src={avatar || ImgAvatarMockup} />
          </div>
          <div
            id="dropdownAccountAction"
            data-dropdown-toggle="dropdown"
            className={classNames("w-6 h-6 cursor-pointer", {
              "rounded-lg bg-grey-primary": showDropdown,
            })}
            onClick={() => setShowDropdown(true)}
          >
            <img src={IconCheck} />
          </div>
        </div>

        <div
          ref={dropDownMenu}
          id="dropdown"
          className={classNames(
            "absolute max-w-[110px] top-8 right-3 min-w-[110px] bg-white divide-y divide-gray-100 rounded-lg shadow w-44 dark:bg-gray-700 z-[1000]",
            {
              hidden: showDropdown === false,
            }
          )}
        >
          <ul
            className="text-sm text-gray-700 dark:text-gray-200"
            aria-labelledby="dropdownAccountAction"
          >
            <li
              onClick={handleLogout}
              className="cursor-pointer flex items-center px-4 py-2 hover:bg-grey-primary dark:hover:bg-grey-primary dark:hover:text-white"
            >
              <img className="w-3 object-fit mr-1" src={IconLogout} />
              Log out
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Header;
