import React from "react";
import { ImgLoadingSummarize } from "../../../assets/image";

const LoadingSummary = () => {
  return (
    <div className="flex flex-col justify-center items-center mt-24 gap-4 w-full">
      <img
        src={ImgLoadingSummarize}
        alt="loading-summarize"
        className="w-16 h-16"
      />
      <p className="text-14px text-neutral-110 m-0">
        Please wait, it may take a few seconds
      </p>
    </div>
  );
};

export default LoadingSummary;
