import classNames from "classnames";

interface IButtonCustom {
  type?: "primary" | "default";
  link?: string;
  disabled?: boolean;
  prefixIcon?: any;
  onClick?: () => void;
  text: string;
  textClass?: string;
  buttonClass?: string;
}
const ButtonCustom = ({
  type = "primary",
  link,
  text,
  disabled = false,
  prefixIcon,
  onClick,
  textClass,
  buttonClass
}: IButtonCustom) => {
  if (onClick) {
    return (
      <div
        className={classNames(
          "rounded-lg h-[40px] w-full flex items-center justify-center cursor-pointer font-bold",
          {
            "bg-purple-dark text-white hover:bg-purple-dark":
              type === "primary",
            "border border-solid border-[#161a27] rounded text-purple-dark":
              type === "default",
            "btn-disabled": disabled,
          },
          buttonClass
        )}
        onClick={disabled ? () => {} : onClick}
      >
        <div className="mr-1" hidden={!prefixIcon}>
          <img src={prefixIcon} />
        </div>
        <span className={textClass}>{text}</span>
      </div>
    );
  }
  return (
    <a
      className={classNames(
        "rounded-lg h-[40px] w-full flex items-center justify-center cursor-pointer",
        {
          "btn-primary": type === "primary",
          "btn-default": type === "default",
          "btn-disabled": disabled,
        }
      )}
      href={link}
      target="_blank"
    >
      <div className="mr-1" hidden={!prefixIcon}>
        <img src={prefixIcon} />
      </div>
      <span>{text}</span>
    </a>
  );
};

export default ButtonCustom;
