/*global chrome*/
import React from "react";
import ButtonCustom from "../../../shared/components/button";
import { ROUTE } from "../../../constants";
import { buildPath } from "../../../shared/utils";
import { ImgLogoLarge } from "../../../assets/image";

const LoginView = () => {
  return (
    <div className="layout">
      <div className="container flex flex-col justify-center items-center">
        <div className="mt-[54px] mb-[54px] flex justify-center">
          <img height={40} width={244} src={ImgLogoLarge} />
        </div>
        <div className="w-full">
          <div className="mb-3">
            <ButtonCustom
              link={buildPath(ROUTE.LOGIN)}
              text="Login"
            ></ButtonCustom>
          </div>
          <div className="mb-3">
            <ButtonCustom
              link={buildPath(ROUTE.FORGOT_PASSWORD)}
              text="Forget Password"
              type="default"
            ></ButtonCustom>
          </div>
          <div className="mb-3">
            <ButtonCustom
              link={buildPath(ROUTE.REGISTER)}
              text="Register"
              type="default"
            ></ButtonCustom>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginView;
