import React, { useEffect } from "react";
import AIService from "../../adapter/services/ai";
import dayjs from "dayjs";
import isToday from "dayjs/plugin/isToday";
import isYesterday from "dayjs/plugin/isYesterday";

dayjs.extend(isToday);
dayjs.extend(isYesterday);

type Item = {
  title: string | null;
  tab_url: string;
  summary: string;
  created_at: string; // ISO string
};

type GroupedData = {
  [dateLabel: string]: Item[];
};

function groupItemsByDate(items: Item[]): GroupedData {
  return items.reduce((groups: GroupedData, item) => {
    const date = dayjs(item.created_at);
    let dateLabel = "";

    if (date.isToday()) {
      dateLabel = `Today, ${date.format("DD MMM YYYY")}`;
    } else if (date.isYesterday()) {
      dateLabel = `Yesterday, ${date.format("DD MMM YYYY")}`;
    } else {
      dateLabel = date.format("DD MMM YYYY");
    }

    if (!groups[dateLabel]) {
      groups[dateLabel] = [];
    }
    groups[dateLabel].push(item);
    return groups;
  }, {});
}
export interface IHistorySummarize {
  title: string;
  tab_url: string;
  summary: string;
  created_at: string;
}
const useGetHistory = (params) => {
  const [loading, setLoading] = React.useState<boolean>(false);
  const [data, setData] = React.useState<IHistorySummarize[]>([]);
  const [total, setTotal] = React.useState<number>(0);
  const [groupedData, setGroupedData] = React.useState<
    Record<string, IHistorySummarize[]>
  >({});

  useEffect(() => {
    AIService.getHistory(params)
      .then((res) => {
        if (res.code === 0) {
          if (params.page === 0) {
            setData(res.data);
            setTotal(res.total);
            const groupedByDate = groupItemsByDate(res.data);
            setGroupedData(groupedByDate);
          } else {
            const newData = [...data, ...res.data];
            setData(newData);
            setTotal(res.total);
            const groupedByDate = groupItemsByDate(newData);
            setGroupedData(groupedByDate);
          }
        }
      })
      .catch((error) => {
        console.error("Error fetching history:", error);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [params]);
  return {
    listGroupedData: Object.keys(groupedData),
    groupedData,
    data,
    total,
    loading,
  };
};

export default useGetHistory;
